.App {
  text-align: center;
}
.wrapper {
    position: relative; /* necessary to push footer to bottom */
    height: auto; /* necessary to push footer to bottom */
    min-height: 100%; /* fills the space when content is less than 100% height of viewport */
    width: 100%;
    margin: 0;
    padding-bottom: 80px; /* creates a space for footer to overlap .wrapper. MUST match footer properties height & margin-top (Footer.css) */
}
.acc-primary {
  color: #00b5e0;
}
.acc-primary-bg {
  background: #00b5e0;
}
.acc-secondary {
  color: #8ec957;
}
.acc-secondary-bg {
  background: #8ec957;
}
.acc-btn {
  padding: 8px 20px;
  margin-right: 10px;
  margin-bottom: 10px;
  font-weight: bold;
  line-height: 1 !important;
  position: relative;
  overflow: hidden;
  text-transform: uppercase;
  -webkit-transition: all 0.4s ease 0s;
  transition: all 0.4s ease 0s;
}
.acc-btn-primary {
  color: #fff;
  border: 1px solid #00b5e0;
  background: #00b5e0;
}
.acc-btn-primary:hover {
  color: #00b5e0;
  border: 1px solid #00b5e0;
  background: transparent;
}
.acc-btn-secondary {
  color: #fff;
  border: 1px solid #8ec957;
  background: #8ec957;
}
.acc-btn-secondary:hover {
  color: #8ec957;
  border: 1px solid #8ec957;
  background: transparent;
}

/* --- Bootstrap Overrides ----------- */
.btn-primary {
  background: #00b5e0;
}

@media (min-width: 992px) {
  .wrapper {
    padding-bottom: 64px; /* creates a space for footer to overlap .wrapper */
  }
}
.acc-navbar {
  background-color: #383838;
  border-radius: 0;
  margin-bottom: 0;
}
.acc-navbar ul.navbar-nav {
  margin: 0 -15px;
}
.acc-header {
  position: relative;
}
.acc-brand {
  padding: 0 15px;
  height: auto;
  line-height: 94px;
}
.acc-brand img {
  position: relative;
  display: inline-block;
  width: 225px;
}
.navbar-inverse .navbar-nav>li.acc-link > a {
  color: #fff;
  font-family: "Roboto Condensed", "Roboto", sans-serif;
  font-size: 16px;
  font-weight: 900;
}
.navbar-inverse .navbar-nav>li.acc-link:hover a {
  color: #00b5e0;
}
.navbar-inverse .navbar-nav>.acc-link.active > a {
  color: #00b5e0;
  background-color: transparent;
}
.navbar-toggle {
  background-color: #00b5e0;
}
.navbar-inverse .navbar-toggle:focus, .navbar-inverse .navbar-toggle:hover {
  background-color: #00b5e0;
}
.nav>li.search-word {
  display: none;
}
.nav>li.search-icon {
  display: block;
}
@media (min-width: 480px) {
  .acc-brand img {
    width: 365px;
  }
}
@media (min-width: 768px) {
  .navbar-nav li a {
    line-height: 94px;
    padding: 0 15px;
  }
  .nav>li.search-word {
    display: block;
  }
  .nav>li.search-icon {
    display: none;
  }
}
@media only screen and (max-width: 767px) {
  .navbar-toggle {
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      -moz-transform: translateY(-50%);
      -webkit-transform: translateY(-50%);
      -o-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
      margin: 0 15px 0;
  }
  .nav>li.search-icon {
    display: block;
    float: right;
  }
  .acc-header.non-admin {
    float: left;
  }
  .nav>li.search-icon a {
    line-height: 80px;
    padding: 0 15px;
  }
}
.clearfix {
  clear: both;
}
footer {
  position: absolute;
  bottom: 0;
  margin-top: -80px;  /* Overlaps the .wrapper padding-bottom by amount entered - MUST be fixed amount. Need to adjust with media queries to ensure responsiveness. */
  height: 80px;  /* MUST match margin-top & .wrapper padding-bottom (App.css). */
  width: 100%;
  background: #000;
  text-align: center;
}
footer p, footer a {
  color: #fff;
  font-family: "Roboto Condensed", "Roboto", sans-serif;
  font-size: 12px;
  font-weight: 900;
  margin-top: 22px;
}
footer p {
  display: block;
  float: none;
  clear: both;
  margin: 22px auto 0;
}
footer a {
  text-decoration: none;
  padding: 8px;
}
footer a:hover {
  text-decoration: none;
  color: #00b5e0;
}
footer li {
  float: left;
  list-style-type: none;
}
footer ul {
  display: inline-block;
  float: none;
  margin: 0 auto;
  -webkit-padding-start: 0px;
          padding-inline-start: 0px;
}
@media (min-width: 480px) {
  footer a {
    padding: 10px;
  }
}
@media (min-width: 992px) {
  footer {
    position: absolute;  /* This and following line position footer at bottom of page. */
    bottom: 0;
    margin-top: -64px;  /* Overlaps the .wrapper padding-bottom by amount entered - MUST be fixed amount. */
    height: 64px;  /* MUST match margin-top & .wrapper padding-bottom (App.css). */
    width: 100%;
    background: #000;
    text-align: center;
  }
  footer p, footer a {
    font-size: 14px;
  }
  footer ul {
    float: right;
    margin-top: 22px;
  }
  footer p {
    float: left;
  }
}
.login {
  max-width: 480px;
  margin: auto;
}
.login header {
  margin: 35px auto 20px;
  padding: 0;
}
.login-input {
  margin: 10px 0;
}
.login .form-group{
  margin-bottom: 0;
}
.login-error {
  margin: 0 auto 10px;
  width: 300px;
  color: #a94442;
}
.login-btn {
  margin: 10px 0;
  width: 100%;
}
.panel.login {
  margin-top: 4em;
  margin-bottom: 4em;
  border: 1px solid #ccc;
}
.pageloader {
  width: 100%;
}

.pageloader img {
  display: block;
  margin: 80px auto 0;
}
